*,
body{
  margin: 0;
  padding: 0;
  font-family: Lato;
  
}

body{
  background-color: #EAEDEF;
  background-image: linear-gradient(-30deg, rgb(57, 186, 230) 50%, white 45%);
}


.wrapper{
  width: 375px;
  height: 95.6vh;
  border: 5px solid black;
  border-radius: 40px;
  margin: 10px auto 10px auto;
}

.logo{
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  width: 151px;
  height: 151px;
  margin-left: 109.5px;
  margin-top: 40px;
  background-color: transparent;
  background-image: linear-gradient(12deg, rgb(57, 186, 230) 50%, white 45%);
  border-radius: 50%;
  border: 3px solid black;
  position: relative;
}

.title{
  align-self: center;
  font-size: 40px;
  color: black;
  text-align: center;
  font-family: 'Marck Script', cursive;
}

.drinks{
  font-family: 'Marck Script', cursive;
  color: whitesmoke;
}

.user_detail{
  padding: 0 0 0 25px;
}

.user_username{
  margin-top: 51px;
}

.error{
  margin-top: 5px;
  font-size: 18px;
  color: rgb(161, 21, 21);
}

label{
  font-size: 20px;
  color: black
}

.input{
  width: 290px;
  height: 51px;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid black;
  font-size: 18px;
  padding: 0 10px;
  color: black;
}

.input_error{
  border-bottom: 2px solid rgb(161, 21, 21);
}

.input:focus{
  outline: none;
}

.user_password{
  margin-top: 40px;
}

.btn_submit{
  margin: 70px 0 0 98px;
  width: 180px;
  height: 45px;
  border-radius: 25px;
  background-color: transparent;
  border: 2px solid whitesmoke;
  font-size: 20px;
  font-weight: bold;
  color: whitesmoke;
  transition: all 800ms ease-in-out;
}

.btn_submit:hover{
  border: 2px solid black;
  color: black;
  background-color: whitesmoke;
}

.btn_submit:focus{
  outline: none;
}

.btn_submit_error{
  background-color: red;
  border: 2px solid red;
  color: white;
}

.btn_submit_success{
  background-color: greenyellow;
  color: rebeccapurple;
}

@media only screen and (max-width: 400px) {
  .wrapper{
    width: 95vw;
  }

  .logo{
    margin-left: 25%
  }

  .input{
    width: 80%;
  }

  .btn_submit{
    margin-left: 20%;
    margin-top: 20px;
  }
}